* {
  box-sizing: border-box;
  font-family: 'Teko', sans-serif; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }


body {
  background: #d7dad7; }

hr {
  width: 80%;
  background-color: rgba(78, 194, 78, 0.63); }

.wrapper {
  margin: 50px auto 100px auto;
  width: 50%;
  display: flex;
  flex-direction: column; }
  .wrapper h1 {
    text-align: center; }
  .wrapper #summary-details {
    text-align: center; }
    .wrapper #summary-details summary {
      margin-bottom: 10px; }

.push-left {
  float: left; }

.push-right {
  float: right; }

.center-item {
  margin: 0 auto !important;
  display: block; }

.summary {
  outline: none;
  margin-bottom: 20px;
  width: 100%; }
  .summary .btn-secondary {
    width: 100%;
    height: 35px;
    border: 0; }

.redirects {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.full-width {
  width: 100%;
  height: 200px !important;
  padding-top: 30px;
  outline: none !important;
  border: 1px solid green; }

.textarea-container {
  width: 49%;
  height: 200px; }
  .textarea-container textarea {
    width: 100%;
    height: 100%; }

.form-control, .error {
  box-shadow: 8px 8px 24px -10px rgba(0, 0, 0, 0.75);
  border: 1px solid green; }

.form-control:focus, .error:focus {
  border: 1px solid #3dbedf;
  box-shadow: 8px 8px 24px -10px rgba(0, 0, 0, 0.75); }

.btn-success {
  margin-top: 50px !important;
  margin-bottom: 50px !important;
  width: 100%; }

.error {
  border: 1px solid red;
  border-radius: 3px; }

.label-separator {
  margin-right: 10px !important; }

.chosen-separator, .separator-container #url-root-part .url-part {
  text-align: center;
  width: 30px;
  border-radius: 3px;
  border: 1px green solid; }

.separator-container {
  position: relative;
  top: 2.5vw; }
  .separator-container #url-root-part {
    margin-left: 20px; }
    .separator-container #url-root-part .url-part {
      width: 220px;
      text-align: left; }

@media screen and (max-width: 1100px) {
  .wrapper {
    width: 90%; }
  .separator-container {
    margin-top: 40px; } }

@media screen and (max-width: 600px) {
  .redirects {
    display: flex;
    flex-direction: column; }
  .textarea-container {
    width: 100%;
    margin-bottom: 40px; } }

a {
  color: white; }

a:visited {
  color: white; }

.style {
  background-color: #218838;
  color: white;
  text-align: center;
  padding: 10px;
  left: 0;
  bottom: 0;
  height: 40px;
  width: 100%;
  position: fixed; }

.phantom {
  display: block;
  height: 60px;
  width: 100%; }

.logo-text {
  font-size: 1.5rem; }

.header a:visited {
  color: black; }

.navbar {
  display: flex;
  flex-direction: row; }

.buttons-flex {
  display: flex;
  flex-direction: row; }

.btn-info {
  background-color: #28A745;
  border-color: #28A745;
  margin-right: 20px; }

.btn-info:hover {
  background-color: #259e41;
  border-color: #28A745;
  margin-right: 20px; }

.h1-tools {
  margin-top: 50px;
  text-align: center !important; }

.element {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  margin: 50px auto;
  text-align: center; }

.inputs {
  display: flex;
  flex-direction: row;
  justify-content: center; }
  .inputs input {
    width: 50%;
    border-radius: 3px; }
  .inputs input:first-child {
    margin-right: 10px; }

.generate {
  width: 100%;
  margin-top: 10px;
  background-color: #28A745;
  color: white; }

.btn-danger {
  width: 100%;
  margin-top: 20px;
  background-color: #28A745;
  color: white;
  border: none; }

.btn-danger:hover {
  background-color: #259e41; }

.shadow-box {
  border: 1px solid black;
  width: 80%;
  margin: 30px auto;
  border-radius: 3px;
  border-color: green;
  box-shadow: 8px 8px 24px -10px rgba(0, 0, 0, 0.75); }

.summary-index-php {
  width: 70%;
  margin: 20px auto !important; }
  .summary-index-php .btn-secondary {
    background-color: #259e41; }
  .summary-index-php .card-body {
    text-align: center; }

@media screen and (max-width: 1100px) {
  .page-body {
    height: 100%; }
  .summary-index-php {
    width: 90%; }
  .btn-danger {
    height: 40%; } }

@media screen and (max-width: 800px) {
  .summary-index-php {
    width: 90%; } }

@media screen and (max-width: 600px) {
  .summary-index-php {
    width: 90%; } }


